import Vue from 'vue'
import Router from 'vue-router'
import AboutUs from '@/components/AboutUs.vue'
import Solution1 from '@/components/Solution1.vue'
import Solution2 from '@/components/Solution2.vue'
import Solution3 from '@/components/Solution3.vue'
import Cooperator from '@/components/Cooperator.vue'
import Consult from '@/components/Consult.vue'
import Production from '@/components/Production.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Production
    },
    {
      path: '/about',
      name: 'about',
      component: AboutUs
    },
    {
      path: '/solution1',
      name: 'solution1',
      component: Solution1
    },
    {
      path: '/solution2',
      name: 'solution2',
      component: Solution2
    },
    {
      path: '/solution3',
      name: 'solution3',
      component: Solution3
    },
    {
      path: '/cooperator',
      name: 'cooperator',
      component: Cooperator
    },
    {
      path: '/consult',
      name: 'consult',
      component: Consult
    },
    {
      path: '/production',
      name: 'production',
      component: Production
    }
  ]
})



